"use client";

import React, { useState } from "react";
import {
    Folder,
    FileText,
    Upload,
    Share2,
    Users,
    X,
} from "lucide-react";

import {
    Table,
    TableBody,
    TableCell,
    TableHeader,
    TableRow,
} from "@/components/ui/table";

import Badge from "@/components/ui/badge/Badge";

/* ===================== Types ===================== */
interface FileItem {
    id: string;
    name: string;
    type: string;
    size: string;
}

interface FolderItem {
    id: string;
    name: string;
    files: FileItem[];
    sharedWith?: string[];
}

interface FileTableItem {
    id: string;
    fileName: string;
    folderName: string;
    type: string;
    size: string;
    access: "Private" | "Shared";
    status: "Available";
}

/* ===================== Static Data ===================== */
const users = ["HR Admin", "Manager", "Finance", "IT Admin"];

const initialMyFolders: FolderItem[] = [
    {
        id: "1",
        name: "Payslips",
        files: [
            { id: "f1", name: "Payslip_Jan.pdf", type: "PDF", size: "250 KB" },
            { id: "f2", name: "Payslip_Feb.pdf", type: "PDF", size: "260 KB" },
        ],
    },
    {
        id: "2",
        name: "Certificates",
        files: [
            { id: "f3", name: "Degree.pdf", type: "PDF", size: "1.2 MB" },
        ],
    },
];

const sharedFolders: FolderItem[] = [
    {
        id: "3",
        name: "HR Policies",
        sharedWith: ["HR Admin"],
        files: [
            { id: "f4", name: "Leave_Policy.pdf", type: "PDF", size: "800 KB" },
        ],
    },
];

/* ===================== Component ===================== */
const EmployeeDocumentHub: React.FC = () => {
    const [activeTab, setActiveTab] = useState<"my" | "shared">("my");
    const [myFolders, setMyFolders] = useState(initialMyFolders);
    const [selectedFolder, setSelectedFolder] = useState<FolderItem | null>(null);

    const [showUpload, setShowUpload] = useState(false);
    const [showShare, setShowShare] = useState(false);

    const [fileName, setFileName] = useState("");
    const [sharedWith, setSharedWith] = useState<string[]>([]);

    const folders = activeTab === "my" ? myFolders : sharedFolders;

    /* ===================== Derived File Table ===================== */
    const allFiles: FileTableItem[] = myFolders.flatMap((folder) =>
        folder.files.map((file) => ({
            id: file.id,
            fileName: file.name,
            folderName: folder.name,
            type: file.type,
            size: file.size,
            access: folder.sharedWith?.length ? "Shared" : "Private",
            status: "Available",
        }))
    );

    /* ===================== Upload ===================== */
    const handleUpload = () => {
        if (!fileName || !selectedFolder) return;

        const newFile: FileItem = {
            id: Date.now().toString(),
            name: fileName,
            type: "PDF",
            size: "300 KB",
        };

        setMyFolders((prev) =>
            prev.map((folder) =>
                folder.id === selectedFolder.id
                    ? { ...folder, files: [...folder.files, newFile] }
                    : folder
            )
        );

        setFileName("");
        setShowUpload(false);
    };

    /* ===================== Share ===================== */
    const handleShare = () => {
        if (!selectedFolder) return;

        setMyFolders((prev) =>
            prev.map((folder) =>
                folder.id === selectedFolder.id
                    ? { ...folder, sharedWith }
                    : folder
            )
        );

        setShowShare(false);
    };

    /* ===================== UI ===================== */
    return (
        <div className="p-6 bg-gray-50 min-h-screen">
            <h1 className="text-2xl font-semibold mb-6">Employee Document Hub</h1>

            {/* Tabs */}
            <div className="flex gap-3 mb-6">
                <Tab active={activeTab === "my"} onClick={() => setActiveTab("my")}>
                    My Documents
                </Tab>
                <Tab active={activeTab === "shared"} onClick={() => setActiveTab("shared")}>
                    Shared With Me
                </Tab>
            </div>

            {/* Layout */}
            <div className="grid grid-cols-12 gap-6">
                {/* Folders */}
                <div className="col-span-4 bg-white rounded-xl border p-4">
                    <h2 className="font-medium mb-4 flex items-center gap-2">
                        <Folder size={18} /> Folders
                    </h2>

                    {folders.map((folder) => (
                        <div
                            key={folder.id}
                            onClick={() => setSelectedFolder(folder)}
                            className={`p-3 rounded-lg cursor-pointer mb-2 hover:bg-gray-100 ${selectedFolder?.id === folder.id ? "bg-gray-100" : ""
                                }`}
                        >
                            {folder.name}
                        </div>
                    ))}
                </div>

                {/* Files */}
                <div className="col-span-8 bg-white rounded-xl border p-6">
                    {!selectedFolder ? (
                        <div className="text-gray-500 text-center mt-20">
                            Select a folder to view documents
                        </div>
                    ) : (
                        <>
                            <div className="flex justify-between items-center mb-4">
                                <h2 className="text-lg font-semibold">{selectedFolder.name}</h2>

                                {activeTab === "my" && (
                                    <div className="flex gap-2">
                                        <ActionButton icon={<Upload size={16} />} onClick={() => setShowUpload(true)}>
                                            Upload
                                        </ActionButton>
                                        <ActionButton icon={<Share2 size={16} />} onClick={() => setShowShare(true)}>
                                            Share
                                        </ActionButton>
                                    </div>
                                )}
                            </div>

                            {selectedFolder.files.map((file) => (
                                <div
                                    key={file.id}
                                    className="flex justify-between items-center p-3 border rounded-lg mb-2"
                                >
                                    <div className="flex items-center gap-3">
                                        <FileText size={18} />
                                        <div>
                                            <p className="font-medium">{file.name}</p>
                                            <p className="text-xs text-gray-500">
                                                {file.type} â€¢ {file.size}
                                            </p>
                                        </div>
                                    </div>
                                    <button className="text-sm text-brand-500">Download</button>
                                </div>
                            ))}

                            {selectedFolder.sharedWith && (
                                <div className="mt-4 text-sm text-gray-500 flex items-center gap-2">
                                    <Users size={16} />
                                    Shared with: {selectedFolder.sharedWith.join(", ")}
                                </div>
                            )}
                        </>
                    )}
                </div>
            </div>

            {/* ===================== All Files Table ===================== */}
            <div className="mt-10">
                <h2 className="text-lg font-semibold mb-4">All My Documents</h2>

                <div className="overflow-hidden rounded-xl border bg-white">
                    <div className="overflow-x-auto">
                        <div className="min-w-[1100px]">
                            <Table>
                                <TableHeader>
                                    <TableRow>
                                        <TableCell isHeader>File Name</TableCell>
                                        <TableCell isHeader>Folder</TableCell>
                                        <TableCell isHeader>Type</TableCell>
                                        <TableCell isHeader>Size</TableCell>
                                        <TableCell isHeader>Access</TableCell>
                                        <TableCell isHeader>Status</TableCell>
                                        <TableCell isHeader>Action</TableCell>
                                    </TableRow>
                                </TableHeader>

                                <TableBody>
                                    {allFiles.map((file) => (
                                        <TableRow key={file.id}>
                                            <TableCell>
                                                <div className="flex items-center gap-2">
                                                    <FileText size={16} />
                                                    {file.fileName}
                                                </div>
                                            </TableCell>
                                            <TableCell>{file.folderName}</TableCell>
                                            <TableCell>{file.type}</TableCell>
                                            <TableCell>{file.size}</TableCell>
                                            <TableCell>
                                                <Badge size="sm" color={file.access === "Shared" ? "primary" : "secondary"}>
                                                    {file.access}
                                                </Badge>
                                            </TableCell>
                                            <TableCell>
                                                <Badge size="sm" color="success">
                                                    {file.status}
                                                </Badge>
                                            </TableCell>
                                            <TableCell>
                                                <button className="text-brand-500 text-sm">Download</button>
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </div>
                    </div>
                </div>
            </div>

            {/* ===================== Modals ===================== */}
            {showUpload && (
                <Modal title="Upload Document" onClose={() => setShowUpload(false)}>
                    <input
                        value={fileName}
                        onChange={(e) => setFileName(e.target.value)}
                        placeholder="File name"
                        className="border rounded-lg px-3 py-2 w-full"
                    />
                    <button
                        onClick={handleUpload}
                        className="mt-4 w-full bg-brand-500 text-white py-2 rounded-lg"
                    >
                        Upload
                    </button>
                </Modal>
            )}

            {showShare && (
                <Modal title="Share Folder" onClose={() => setShowShare(false)}>
                    {users.map((user) => (
                        <label key={user} className="flex items-center gap-2 mb-2">
                            <input
                                type="checkbox"
                                checked={sharedWith.includes(user)}
                                onChange={(e) =>
                                    setSharedWith((prev) =>
                                        e.target.checked
                                            ? [...prev, user]
                                            : prev.filter((u) => u !== user)
                                    )
                                }
                            />
                            {user}
                        </label>
                    ))}
                    <button
                        onClick={handleShare}
                        className="mt-4 w-full bg-brand-500 text-white py-2 rounded-lg"
                    >
                        Share
                    </button>
                </Modal>
            )}
        </div>
    );
};

/* ===================== UI Helpers ===================== */
const Tab = ({ active, children, onClick }: any) => (
    <button
        onClick={onClick}
        className={`px-4 py-2 rounded-lg border ${active ? "bg-brand-500 text-white" : "bg-white"
            }`}
    >
        {children}
    </button>
);

const ActionButton = ({ icon, children, onClick }: any) => (
    <button
        onClick={onClick}
        className="flex items-center gap-2 px-3 py-2 border rounded-lg text-sm"
    >
        {icon}
        {children}
    </button>
);

const Modal = ({ title, children, onClose }: any) => (
    <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
        <div className="bg-white rounded-xl p-6 w-[400px] relative">
            <button onClick={onClose} className="absolute top-3 right-3">
                <X size={18} />
            </button>
            <h3 className="text-lg font-semibold mb-4">{title}</h3>
            {children}
        </div>
    </div>
);

export default EmployeeDocumentHub;
